import React from "react"
import Building2Icon from "@assets/svg/LucidIcons/building2.svg"
import UsersIcon from "@assets/svg/LucidIcons/users.svg"
import BookOpenIcon from "@assets/svg/LucidIcons/book_open.svg"
import HeartIcon from "@assets/svg/LucidIcons/heart.svg"
import MusicIcon from "@assets/svg/LucidIcons/music.svg"
import PresentationIcon from "@assets/svg/LucidIcons/presentation.svg"
import LandmarkIcon from "@assets/svg/LucidIcons/landmark.svg"
import GraduationCapIcon from "@assets/svg/LucidIcons/graduation_cap.svg"

import { UnitCard } from "./UnitCards"
import { Container, Grid, Section } from "./styled"
import { Typography } from "@mui/material"

const units = [
  {
    icon: <Building2Icon />,
    title: "Samithi",
    description:
      "The foundational unit at village/town level that conducts regular devotional activities and service projects for local community development."
  },
  {
    icon: <UsersIcon />,
    title: "Bhajan Mandali",
    description:
      "Groups dedicated to devotional singing and spiritual practices, fostering unity and devotion among members through regular gatherings."
  },
  {
    icon: <BookOpenIcon />,
    title: "Bal Vikas",
    description:
      "Educational wing focusing on children's character development through value-based education and spiritual teachings."
  },
  {
    icon: <HeartIcon />,
    title: "Seva Dal",
    description:
      "Service wing organizing and conducting various service activities, medical camps, and disaster relief operations."
  },
  {
    icon: <MusicIcon />,
    title: "Mahila Vibhag",
    description:
      "Women's wing empowering women through spiritual and service activities, promoting family values and cultural preservation."
  },
  {
    icon: <PresentationIcon />,
    title: "Youth Wing",
    description:
      "Dynamic group of young adults engaged in service projects, leadership development, and spiritual growth activities."
  },
  {
    icon: <LandmarkIcon />,
    title: "District Level",
    description:
      "Coordinates and oversees activities of all Samithis within the district, ensuring proper implementation of organization guidelines."
  },
  {
    icon: <GraduationCapIcon />,
    title: "State Level",
    description:
      "Provides leadership and direction to all districts, maintaining organizational standards and facilitating large-scale service initiatives."
  }
]

const UnitsSection: React.FC = () => {
  return (
    <Section>
      <div className="header-container">
        <Typography variant="h1Bold" color="primary.main">
          Our Functional Units
        </Typography>
        <Typography variant="h4" color="primary.main">
          Each unit serves a unique purpose in our mission of spiritual
          transformation and societal service
        </Typography>
      </div>
      <Container>
        <Grid>
          {units.map((unit, index) => (
            <UnitCard key={index} {...unit} />
          ))}
        </Grid>
      </Container>
    </Section>
  )
}

export default UnitsSection
