import { NonMuiColors } from "@constants/COLORS"
import { styled } from "@mui/material"
import { pxToRem } from "@utils/styles" // Assuming your pxToRem utility

export const Section = styled("section")(({ theme }) => ({
  display: "flex",
  flexDirection: "column",
  justifyContent: "center",
  alignItems: "center",
  gap: pxToRem(32),
  background: theme.palette.background.paper,
  padding: pxToRem(32),
  "& .header-container": {
    textAlign: "center"
  }
}))

export const Container = styled("div")({
  padding: pxToRem(16)
})

export const Grid = styled("div")(({ theme }) => ({
  display: "grid",
  gridTemplateColumns: "1fr",
  gap: pxToRem(32),
  [theme.breakpoints.up("sm")]: {
    gridTemplateColumns: "repeat(2, 1fr)"
  },
  [theme.breakpoints.up("lg")]: {
    gridTemplateColumns: "repeat(3, 1fr)"
  }
}))

export const CardWrapper = styled("div")(({ theme }) => ({
  backgroundColor: NonMuiColors.WHITE,
  borderRadius: pxToRem(8), // rounded-lg
  boxShadow: theme.shadows[1],
  padding: pxToRem(24), // p-6
  transition: "transform 0.2s ease-in-out, box-shadow 0.2s ease-in-out", // transition-transform duration-200, transition-shadow
  display: "flex",
  flexDirection: "column",
  "&:hover": {
    transform: "translateY(-4px)", // hover:-translate-y-1
    boxShadow: theme.shadows[4]
  }
}))

export const CardHeader = styled("div")({
  display: "flex",
  alignItems: "center",
  marginBottom: pxToRem(16),
  gap: pxToRem(12)
})

export const CardIcon = styled("div")(({ theme }) => ({
  color: theme.palette.primary.main
}))
