import React from "react"
import { Content, Overlay, Wrapper } from "./styled"
import { Typography } from "@mui/material"

const HeroSection: React.FC = () => {
  return (
    <Wrapper
      style={{
        backgroundImage:
          'url("https://images.unsplash.com/photo-1470075801209-17f9ec0cada6?auto=format&fit=crop&w=2000&q=80")'
      }}
    >
      <Overlay />
      <Content>
        <Typography variant="h1Bold">Units and Their Functioning</Typography>
        <Typography variant="h4">
          A harmonious network of spiritual and service units working together
        </Typography>
      </Content>
    </Wrapper>
  )
}

export default HeroSection
